@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Heavy.ttf);
  font-weight: 900;
}

* {
  box-sizing: border-box;
}

html {
  font-family: "Avenir", sans-serif;
  font-size: 16px;
}

.search-form {
  margin: 20px 0;
  position: relative;
}

.image {
  position: absolute;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
  cursor: pointer;
  color: #687c94;
}

.input {
  font-family: inherit;
  font-size: inherit;
  line-height: 22px;
  color: #3d4e61;
  width: 100%;
  height: 70px;
  padding-left: 62px;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  outline: none;
  border-radius: 3px;
}

.small--image {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.small--input {
  font-family: inherit;
  font-size: 14px;
  line-height: 19px;
  height: 42px;
  padding-left: 33px;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
}
